import tabBar from '@/components/index/TabBar.vue'
import uniSection from '@/components/unis/uni-section/uni-section.vue'
export default {
	components:{
		tabBar,
		uniSection
	},
	data(){
		return {
			pageTitle:'工作台',
			contentHeight:'',
			colorList:[
				'#FF5345,#FF867D',
				'#2298ef,#64B5F6',
				'#00BCD5,#4DD0E2',
				'#8BC24A,#AED582',						
			]
		}
	},
	computed:{
		colorLength(){
			return this.colorList.length;
		},
		menuTree1(){
			return this.$store.getters.menuTree1;
		}
	},
	methods: {
		async toTable(menu){
			if(menu && menu.pageName == '个人中心'){
				this.toMypage()
			}else if(menu){
				this.$store.commit('updatePath',menu.path);
				let res = await this.$store.dispatch('getPath').catch(errpr=>{});
				if(res){
					uni.reLaunch({
						url:'/pages/index/index'
					})
				}else{
					uni.showToast({
						title:'页面请求失败',
						icon:'none'
					})
				}						
			}else{
				uni.showToast({
					title:'无菜单',
					icon:'none'
				})
			}	
		},
		toMypage(){
			uni.navigateTo({
				url:"/pages/office/my/my"
			})
		}		
	},
	onShow() {
		if(!this.loginState){
			this.$store.dispatch('changeMenuTree')
		}	
		console.log('onshow')
	}
}
